@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap);
:root{

  /* COLOR PALLETS */
  --primary:hsl(271, 90%, 60%);
  --primary-light:hsl(271, 100%, 90%);
  --primary-dark:hsl(271, 44%, 75%);

  --secondary-dark:hsl(346, 100%, 70%);
  --secondary-light:hsl(346, 100%, 80%);

  --dark:hsl(258, 80%, 10%);
  --light: white;
  
  /* fonts */
  font-family: 'Open sans', sans-serif;

}

*{
  margin:0;
  padding:0;
}
 
::-webkit-scrollbar{
  background: hsl(258, 80%, 10%);
  background: var(--dark);
  width:.7rem; 
}

::-webkit-scrollbar-thumb{
  width: 0.5em;
  border-radius: 1rem;
  background: white;
  background: var(--light);
}
 
.header{
    position:fixed;
    top:0;
    z-index:9;
    display:flex;
    
    justify-content: space-between;
    align-items: center;
    width:100%;
    height:4rem;
    /* background: var(--primary-light); */
}
.header .logo{
    position: relative;
    margin-left: 1rem;
    width:3.5rem;
    height: 3.5rem;
    background-image: url(/F3_project/static/media/f3logo.412f126e.jpeg);
    background-size: cover;
}
.header .logo:hover::before{
    content: '';
    display: block;
    position: absolute;
    inset:0;
    z-index:1;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  
    /* background: white; */
    border: 3px solid white;
 
}
.header .nav{
    margin-right: 2rem;
    display: flex;
    align-items: center;
    grid-gap:.5rem;
    gap:.5rem;
    height:3rem;
    text-transform: capitalize;
}
.nav .options{
    position: relative;
    width:100%;
    height: 100%;
    text-transform: capitalize;
    /* border: 2px solid red; */
}
.navtoggle:checked + .options .menus{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    opacity: 1;
}
.options .menus{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    grid-gap:1rem;
    gap:1rem;
    padding:.8rem;
    /* border: 2px solid red; */
    background: var(--dark);
    -webkit-transform: scale(0,1);
            transform: scale(0,1);
    -webkit-transform-origin: right;
            transform-origin: right;
    opacity: 0;
    transition: opacity .2s ease, -webkit-transform .3s ease;
    transition: transform .3s ease, opacity .2s ease;
    transition: transform .3s ease, opacity .2s ease, -webkit-transform .3s ease;
}
.options .menus .link{
    position: relative;
    text-decoration: none;
    color: white;
}
.options .menus .link::after{
    content:'';
    display: block;
    position: absolute;
    width:100%;
    height:.1rem;
    background:white;
    -webkit-transform: scale(0,1);
            transform: scale(0,1);
}
.options .menus .link:hover::after{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
}
.options .menus .link.menu_act::after{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    -webkit-transform-origin: left;
            transform-origin: left;
    transition: -webkit-transform .3s ease;
    transition: transform .3s ease;
    transition: transform .3s ease, -webkit-transform .3s ease;
}

.navtoggle:checked ~ .options .head{
    -webkit-transform: scale(0,1);
            transform: scale(0,1);
    opacity: 0;
    transition: opacity .2s ease,-webkit-transform .2s ease;
    transition: transform .2s ease,opacity .2s ease;
    transition: transform .2s ease,opacity .2s ease,-webkit-transform .2s ease;
    
}
  .head{
    /* display: none; */
    position:absolute;
    top:0;
    right:0;
    display: grid;
    padding: 0rem .5rem;
    place-items: center;
    height:100%;
    font-size: clamp(.8rem,5vw,1.3rem);
    font-weight: 100;
    background: var(--dark);
    color: white;
    
    -webkit-transform: scale(1,1);
    
            transform: scale(1,1);
    -webkit-transform-origin:right;
            transform-origin:right;
    /* border: 2px solid red; */
}

/* Burger */
.navtoggle{display: none;}
.header label{
    position: relative;
    display: grid;
    place-items: center;
    width:3.3rem;
    height:100%;
    background:var(--dark);    
}
.header label span{
    position: absolute;
    width:70%;
    height:.25rem;
    background: white;
    transition: all .3s ease-in-out;
} 
.header label span:first-child{
   top:.8rem;
}
.header label span:nth-child(2){
   top:1.4rem;
}
.header label span:last-child{
   bottom:.7rem;
}
    /* animate - burger*/
.header .navtoggle:checked ~ label span:first-child{
    -webkit-transform: rotate(45deg) translate(25%,150%);
            transform: rotate(45deg) translate(25%,150%);
}
.header .navtoggle:checked ~ label span:nth-child(2){
    opacity: 0;
}
.header .navtoggle:checked ~ label span:last-child{
    -webkit-transform: rotate(-45deg) translate(25%,-150%);
            transform: rotate(-45deg) translate(25%,-150%);
}
/* end - Burger */


/* RESPONSIVE */
@media (max-width:600px){
    .header .logo{
        margin-left: 1rem;
        max-width:2.5rem;
        max-height: 2.5rem;
       
    }
    .header .nav{
        margin-right: 1rem;
    }
}
@media (max-width:480px){
    .header .logo{
        margin-left: .5rem;
    }
    .options .menus{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .header label{
        width:4rem;
        height:100%;
    }
    .options .menus .link{
        font-size: clamp(.7rem,5vw,1.3rem);
    }
     
}
.landing{
    position:relative;
    width:100%;
    min-height:100vh;
}
.landing::before{
    content: '';
    position: fixed;
    width: 100vw;
    height: 100vh;
    background: var(--primary-dark);
    background-image: url(data:image/png;base64,);
    background-position: 100% 100%;
    background-size: cover;
}
.landing .content{
    position: absolute;
    width: 100%; 
}
.landing .content .nav{
 
    margin:3%;
    display: flex;
    flex-wrap: wrap;
    grid-gap:2rem;
    gap:2rem;
    justify-content: center;
    align-items: center;
    list-style: none;

    font-size: 1.2rem;
}
.landing .nav li {
    color: var(--secondary-dark);
    padding: .5rem;
    background-color: var(--secondary-light);
    border-radius: 5px;
}
.landing .nav li:hover{
    box-shadow: 0 0 4px var(--dark);
}
 
.landing .content > h1{
    margin-top: 5rem;
    margin-bottom: min(10%,5rem);
    font-weight:300;
    font-size: clamp(2.5rem,10vw,7rem);
    text-align: center;
    color: var(--dark);
}
.landing .content > .notes{
    margin-top: 3rem;
    display: flex;
    grid-gap:2rem;
    gap:2rem;
    justify-content: center;
    padding: .5rem;
    background: var(--secondary-dark);
    list-style: none;
}
.landing .content > .notes .main{
    font-size: 1.1rem;
    font-weight: 500;
}
.landing > .notes a{
    
    color:var(--dark)
}

/* SECTIONS */
.landing .m_sections{
    
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    grid-gap:1rem;
    gap:1rem;
    margin: 1rem;
}
.landing .m_sections .card{
    position: relative;

    --card-width: min(40vw, 15rem);
    width: var(--card-width);
    height: var(--card-width);
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 2px 2px 5px rgba(0,0,0,.25);
}
.landing .m_sections .card .image{
    position: absolute;
    z-index: 1;
    width: var(--card-width);
    height:var(--card-width);
   
    
    object-fit: cover;
    object-position: 50% 50%;
}
.landing .m_sections .card::before{
    content: '';
    position: absolute;
    width: 100%;
    height:calc(var(--card-width) / 2);
    z-index: 2;
    background: rgba(0,0,0,.65);

    -webkit-transform: scale(1,0);

            transform: scale(1,0);
    -webkit-transform-origin: top;
            transform-origin: top;
}
.landing .m_sections .card::after{
    content: '';
    position: absolute;
    bottom:0;
    width: 100%;
    height:calc(var(--card-width) / 2);
    z-index: 2;
    background: rgba(0,0,0,.65);

    -webkit-transform: scale(1,0);

            transform: scale(1,0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
}
.landing .m_sections .card:hover::before,
.landing .m_sections .card:hover::after{
    -webkit-transform: scale(1,1);
            transform: scale(1,1);
    transition: -webkit-transform .25s ease-in-out;
    transition: transform .25s ease-in-out;
    transition: transform .25s ease-in-out, -webkit-transform .25s ease-in-out;
}
 
.landing .m_sections .card .title{
    color: var(--light);
    cursor:pointer;
}
.landing .m_sections .card .title::before{
    content:attr(data-des);
    position: absolute;
    inset:0;
    z-index: 3;
    display: grid;
    place-items: center;
    text-align: left;
   
    font-size:clamp(.6rem, 1.5vw, .9rem);
    padding:1.2rem;
    opacity: 0;
}
 
.landing .m_sections .card:hover .title::before{
    opacity: 1;
    transition: opacity .5s ease-out;
}
.landing .m_sections .card .title::after{
    content: attr(data-title);
    position: absolute;
    bottom: 0;
    left: 0;
    width:100%;
    z-index: 4;

    text-align: center;
    padding: .5rem 0rem;
    font-size:clamp(.5rem,4vw,1.2rem);
    background: rgba(0,0,0,.65);
    opacity: 1;
    transition: opacity .6s ease-out;
}
.landing .m_sections .card:hover .title::after{
    opacity: 0;
    transition: opacity .2s ease-out;
}

/* Tabs Version */
@media screen and (min-width:602px) and (max-width:920px){
    .landing .m_sections .card{
        --card-width:min(20vw,13rem)
    }
    .landing .m_sections .card .title::after{
        font-size:clamp(.5rem,2vw,1.1rem);
        padding: .3rem 0rem;
    }
}
